.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;

}


.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,transparent,var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image{
    margin-bottom: 4rem;
    border-radius: 2rem;
    overflow: hidden;
    transform :rotate(0deg); /* rotate the image slightly */ 
    transition: var(--transiiton);

}
.about__me-image :hover{
    transform :rotate(0); /* rotate the image slightly */ 

}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.5;
}

.about__card{
    background: var(--color-bg-variant);
    border:1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
   

}

.about__card:hover{

    background:transparent;
    border-color: var(--color-primary-variant);
   

    cursor: default;

}

.about__icons{

    color: var(--color-primary);
    font-size: 12rem;
    margin-bottom: 10rem;
 
}


.about__card h5{
    font-size: 0.95rem;
    color: #000;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
    
}


.about__content p{
    margin: 12rem 0 2.5rem;
    color: var(--color-light);

}


/*================== m e d i u m  d e v i c e s===================*/

@media screen and (max-width: 1024px){

    .about__container{
        grid-template-columns: 1fr;
        gap:0;

    }

    .about__me { 
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{
        margin: 1rem 0 1.5rem;


    }


}

/*================== s m a l l  d e v i c e s ===================*/

@media screen and (max-width: 600px){
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;

    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;

    }

    .about__content{
        text-align: center;
    }

    .about__content{
        text-align: center;

    }

    .about__content p{
        margin: 1.5rem 0;
    }



}